<template>
    <section>
        <tg-list :IsShowAction="false" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset"
            :PageSize="PageSize" IdentityColumn="ASSET_CATALOGUE_ID" HeaderText="Product Write-Off"
            :ListData="ItemWriteOffData" :HeaderData="Headers" :widgets="widgets" :showCheckBox="false"
            :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :NorecordfoundText="$t('NoRecordfound')"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                
                <template v-if="data.column.COLUMN_NAME == 'Writeoffqty'">
                    <span class="text-dark" :title="data.row.Writeoffqty">
                        <i class="fa fa-shopping-basket" aria-hidden="true"></i>
                        <span class="noti-circle noti-blue">{{ data.row.Writeoffqty > 0 ? data.row.Writeoffqty
            : 0
                            }}</span></span>
                </template>
            </template>
        </tg-list>

    </section>
</template>

<script>
import Schema from "../AssetCatalog/ItemWriteOffListSchema"
import DataService from '../../service/DataService'

export default {
    components: {

    },
    data() {
        return {
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            LegendArray: [],
            ItemWriteOffData: [],
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            PageSizeOption: null,
            StatusListForListing: [],
            searchCondition: "",
            Headers: [{
                "COLUMN_NAME": "ItemCode",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product Code",
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "ItemName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Product Name",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "UniqueName",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Unique Name",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "LocationName",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Location",
                "DISPLAY_ORDER": 4,

            },
            {
                "COLUMN_NAME": "Writeoffqty",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Available Quantity",
                "DISPLAY_ORDER": 5,
                settings: {
                    isInSlot: true
                }

            },
            {
                "COLUMN_NAME": "PurchaseCost",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Purchase Value",
                "DISPLAY_ORDER": 6,

            },

            {
                "COLUMN_NAME": "BookCurrentCost",

                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Current book Value",
                "DISPLAY_ORDER": 7,

            },
            {
                "COLUMN_NAME": "SalvageCost",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Salvage Cost",
                "DISPLAY_ORDER": 8,
            },
            {
                "COLUMN_NAME": "WriteoffCost",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Write-Off Value",
                "DISPLAY_ORDER": 9,
            }

            ],
            noRecord: true,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SortBy: "",
            SortExp: "",

            legendProgress: [{
                class: '',
                backgroundColor: '#17a2b8',
                name: 'Draft'
            },
            {
                class: 'legend-Progress',
                backgroundColor: '',
                name: 'Approved'
            },
            {
                class: 'legend-pending',
                backgroundColor: '',
                name: 'Pending'
            },
            {
                class: '',
                backgroundColor: '#fe6847',
                name: 'SendForCorrection'
            },
            {
                class: '',
                backgroundColor: '#ee1313',
                name: 'Rejected'
            }
            ],
        }
    },
    async created() {
        var vm = this;
        await vm.GetPageSize();
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "Location");
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "Product Name/Code");
        vm.FetchData();

    },
    methods: {
        columnDataFormatter: function (row, column) {
            return row[column.COLUMN_NAME];
        },
        async GetPageSize() {
            var vm = this;
            var params = 'key=DefaultPageSize'
            await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {
                if (response.data != null) {
                    vm.PageSize = parseInt(response.data);
                }
            });
        },
        FetchData() {
            var vm = this;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&statusIds=&${vm.searchCondition}`;
            DataService.ItemWriteOffList(url).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {

                        vm.ItemWriteOffData = response.data;
                        vm.TotalRecords = response.data[0].TotalRecords;
                        vm.TotalPages = Math.ceil(response.data[0].TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.noRecord = false;
                    } else {
                        vm.noRecord = true;
                        vm.ItemWriteOffData = [];
                        vm.TotalRecords = 0
                    }
                }
                setTimeout(function () {
                    vm.ResponsiveDataTable('tablelistingdata')
                }, 500);
            });
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        currentPageMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        GetPageSizeOption: function () {
            var list = [];
            var defaultPageSize = 10;
            for (var i = defaultPageSize; i < defaultPageSize + 95; i += 5) {
                list.push(i);
            }
            this.PageSizeOption = list;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },

        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "addnew":
                    this.OpenItemWriteOff();
                    break;
            }
        },
        OpenItemWriteOff() {
            var vm = this;
            vm.$router.push({
                path: 'ItemWriteOff'
            });
        },
        sortdata: function (s) {
            debugger;
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

    },
}
</script>

<style scoped>
.vue-treeselect__input {
    border: 1px solid #abb5bf;
    padding: 4px 12px;
}

.vue-treeselect__single-value {
    padding-left: 12px;
}
</style>
